import ExtensionPage from 'flarum/components/ExtensionPage';
import Button from 'flarum/common/components/Button';
import saveSettings from 'flarum/admin/utils/saveSettings';
import Switch from 'flarum/common/components/Switch';

export default class SettingsPage extends ExtensionPage {
  oninit(attrs) {
    super.oninit(attrs);
    this.loading = false;
    this.modified = false;

    this.userSelfQueryUrl = app.data.settings['user-self-query.userSelfQueryUrl'];

  }

  content() {
    return (
      <div className="ExtensionPage-settings FlarumBadgesPage">
        <div className="container">

          <fieldset className="Form-group">
            <label>网站列表</label>
            <textarea style="height:300px;" disabled={this.loading} className="FormControl" value={this.userSelfQueryUrl} oninput={(e: InputEvent) => {
                this.modified = true;
                this.userSelfQueryUrl = (e.currentTarget as HTMLInputElement).value;
              }} />
          </fieldset>

          <Button
            onclick={this.customSaveSettings.bind(this)}
            className="Button Button--primary"
            disabled={!this.modified}
            loading={this.loading}
          >
            保存
          </Button>
        </div>
      </div>
    );
  }

  async customSaveSettings(e: SubmitEvent) {
    e.preventDefault();

    app.alerts.clear();
    this.loading = true;

    await saveSettings({
      'user-self-query.userSelfQueryUrl': this.userSelfQueryUrl,
    });

    this.onSettingsSaved();
    return true;
  }

  onSettingsSaved(): void {
    this.loading = false;
    this.modified = false;

    app.alerts.show({ type: 'success' }, app.translator.trans('core.admin.settings.saved_message'));
  }

}
